import { showText } from '@/utils/index';
export default {
  name: 'PageOne',
  data() {
    return {
      showDoctor: false
    };
  },
  props: {
    msg: String
  },
  mounted() {
    showText('text1Id', 'text1').then(() => {
      this.showDoctor = true;
    });
  },
  methods: {
    onNextPage() {
      if (this.showDoctor) {
        this.$emit('next');
      }
    }
  }
};